.block-out-box {
  height: 90vh;
  width: 90vw;
  background: rgba(0, 0, 0, 0.9);
  position: fixed;
  z-index: 2;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border-radius: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  color: white;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  text-align: center;
}

.block-out-box h1 {
  font-size: 3em;
  margin: 10px;
}

.block-out-box p {
  margin: 10px;
}

.start-button {
  background: rgba(128, 128, 128, 0.562);
  border: 1px solid lime;
  color: white;
  padding: 20px;
  border-radius: 10px;
  margin: 20px;
}

.start-button:hover {
  opacity: 0.5;
  cursor: pointer;
}

.timer {
  -ms-grid-column-align: center;
  justify-self: center;
  -ms-grid-row-align: center;
  align-self: center;
  color: white;
  font-size: 2em;
  grid-column: 1 / -1;
  grid-row: 1 / -1;
}

form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

#name {
  margin: 20px;
  height: 25px;
}

@media (max-width: 1180px) {
  .block-out-box h1 {
    font-size: 2em;
  }

  .block-out-box p {
    font-size: 0.5em;
  }
}

@media (max-width: 850px) {
  .timer {
    -ms-grid-column-align: end;
    justify-self: end;
    margin: 20px;
    font-size: 1.5em;
  }
}
