.picture {
  height: 100%;
  width: 100%;
}

img {
  height: 100%;
}

.selector {
  position: absolute;
  border: 5px solid rgba(0, 0, 0, 0.65);
  border-radius: 100%;
  height: 50px;
  width: 50px;
  -webkit-animation: createCircle 0.5s;
  animation: createCircle 0.5s;
}

@-webkit-keyframes createCircle {
  from {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes createCircle {
  from {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.correct {
  position: absolute;
  border: 5px solid rgb(153, 255, 0);
  border-radius: 100%;
  height: 50px;
  width: 50px;
}

.menu {
  position: absolute;
  background: rgba(128, 128, 128, 0.75);
  height: auto;
  width: 100px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-animation: open 0.75s;
  animation: open 0.75s;
  overflow: hidden;
}

@-webkit-keyframes open {
  from {
    width: 0;
  }
  to {
    width: 100px;
  }
}

@keyframes open {
  from {
    width: 0;
  }
  to {
    width: 100px;
  }
}

.person-selector {
  background: rgba(128, 128, 128);
  color: white;
  border: 1px solid black;
  margin: 1px;
  padding: 2px;
}

.person-selector:hover {
  opacity: 0.8;
  cursor: pointer;
}
